.login-signup {

    .alert-success {
        background-color: #d1e7dd;
        padding: 15px;
        width: 32%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: auto;
        border-radius: 5px;
        border: 1px solid red;

        .space {
            margin: 5px;
        }
    }

    .error-msg {
        margin: 1em auto;
        color: red;
    }

    input {
        color: black;
        z-index: 1;
    }

    fieldset {
        background-color: $primary-clr-100;
    }

    @media screen and (min-width: 600px) {
        .alert-success {
            background-color: #d1e7dd;
            flex-direction: row;
        }
    }
}


